'use client';

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import Layout from '@/components/Layout';
import SearchBar from '@/components/SearchBar';
import { fetchAllClients, BitrixClient } from '@/lib/api/bitrix';
import { SearchResult } from '@/lib/api/search';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Alert, AlertDescription } from '@/components/ui/alert';
import { Skeleton } from '@/components/ui/skeleton';
import { ScrollArea } from '@/components/ui/scroll-area';
import { Progress } from '@/components/ui/progress';

export default function AdminDashboard() {
  const router = useRouter();
  const [clients, setClients] = useState<BitrixClient[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState('');
  const [selectedResult, setSelectedResult] = useState<SearchResult | null>(null);

  useEffect(() => {
    loadClients();
  }, []);

  const loadClients = async () => {
    try {
      setIsLoading(true);
      setError('');
      const clientData = await fetchAllClients();
      // Add more mock clients for better scrolling demonstration
      const additionalClients = [
        {
          id: '3',
          name: 'Digital Innovations Inc',
          email: 'hello@digitalinnovations.com',
          phone: '+1-555-0789',
          company: 'Digital Innovations',
          status: 'Active',
          createdDate: '2024-03-05',
          lastActivity: '2024-12-18'
        },
        {
          id: '4',
          name: 'Global Enterprises',
          email: 'contact@globalenterprises.com',
          phone: '+1-555-0321',
          company: 'Global Enterprises',
          status: 'Pending',
          createdDate: '2024-04-12',
          lastActivity: '2024-12-17'
        },
        {
          id: '5',
          name: 'StartupXYZ',
          email: 'team@startupxyz.com',
          phone: '+1-555-0654',
          company: 'StartupXYZ',
          status: 'Active',
          createdDate: '2024-05-20',
          lastActivity: '2024-12-16'
        },
        {
          id: '6',
          name: 'Enterprise Solutions',
          email: 'info@enterprisesolutions.com',
          phone: '+1-555-0987',
          company: 'Enterprise Solutions',
          status: 'Active',
          createdDate: '2024-06-08',
          lastActivity: '2024-12-15'
        }
      ];
      setClients([...clientData, ...additionalClients]);
    } catch (err) {
      setError('Failed to load client data. Please try again.');
      console.error('Error loading clients:', err);
    } finally {
      setIsLoading(false);
    }
  };

  const getStatusBadgeVariant = (status: string) => {
    switch (status.toLowerCase()) {
      case 'active':
        return 'default';
      case 'inactive':
        return 'secondary';
      case 'pending':
        return 'outline';
      default:
        return 'secondary';
    }
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric'
    });
  };

  const handleSearchResultSelect = (result: SearchResult) => {
    setSelectedResult(result);
  };

  const handleClientClick = (clientId: string) => {
    router.push(`/admin/client/${clientId}`);
  };

  const getClientGrowthRate = () => {
    const currentMonth = clients.filter(client => {
      const created = new Date(client.createdDate);
      const now = new Date();
      return created.getMonth() === now.getMonth() && created.getFullYear() === now.getFullYear();
    }).length;
    
    const lastMonth = clients.filter(client => {
      const created = new Date(client.createdDate);
      const now = new Date();
      const lastMonthDate = new Date(now.getFullYear(), now.getMonth() - 1);
      return created.getMonth() === lastMonthDate.getMonth() && created.getFullYear() === lastMonthDate.getFullYear();
    }).length;
    
    if (lastMonth === 0) return 100;
    return Math.round(((currentMonth - lastMonth) / lastMonth) * 100);
  };

  const getClientRetentionRate = () => {
    const activeClients = clients.filter(client => client.status === 'Active').length;
    const totalClients = clients.length;
    return totalClients > 0 ? Math.round((activeClients / totalClients) * 100) : 0;
  };

  return (
    <Layout title="Admin Dashboard" allowedRoles={['admin']}>
      <div className="space-y-6">
        {/* Search Bar */}
        <div className="flex justify-center">
          <SearchBar 
            onResultSelect={handleSearchResultSelect}
            placeholder="Search clients, customers, or projects..."
          />
        </div>

        {/* Search Result Display */}
        {selectedResult && (
          <Card className="border-blue-200 bg-blue-50">
            <CardHeader>
              <CardTitle className="text-lg flex items-center space-x-2">
                <span>🔍</span>
                <span>Search Result</span>
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="flex items-center space-x-3">
                <Badge className="capitalize">{selectedResult.type}</Badge>
                <div>
                  <p className="font-medium">{selectedResult.title}</p>
                  <p className="text-sm text-gray-600">{selectedResult.subtitle}</p>
                  {selectedResult.description && (
                    <p className="text-xs text-gray-500 mt-1">{selectedResult.description}</p>
                  )}
                </div>
              </div>
            </CardContent>
          </Card>
        )}

        {/* Enhanced Analytics Cards */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">Total Clients</CardTitle>
              <span className="text-2xl">🏢</span>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">
                {isLoading ? <Skeleton className="h-8 w-16" /> : clients.length}
              </div>
              <p className="text-xs text-muted-foreground">
                Active client accounts
              </p>
              <div className="mt-2">
                <Progress value={85} className="h-2" />
                <p className="text-xs text-gray-500 mt-1">85% capacity</p>
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">Client Growth</CardTitle>
              <span className="text-2xl">📈</span>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">
                {isLoading ? (
                  <Skeleton className="h-8 w-16" />
                ) : (
                  `+${getClientGrowthRate()}%`
                )}
              </div>
              <p className="text-xs text-muted-foreground">
                This month vs last month
              </p>
              <div className="mt-2">
                <Progress value={getClientGrowthRate()} className="h-2" />
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">Retention Rate</CardTitle>
              <span className="text-2xl">🎯</span>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">
                {isLoading ? (
                  <Skeleton className="h-8 w-16" />
                ) : (
                  `${getClientRetentionRate()}%`
                )}
              </div>
              <p className="text-xs text-muted-foreground">
                Active client retention
              </p>
              <div className="mt-2">
                <Progress value={getClientRetentionRate()} className="h-2" />
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium">Recent Activity</CardTitle>
              <span className="text-2xl">⚡</span>
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">
                {isLoading ? (
                  <Skeleton className="h-8 w-16" />
                ) : (
                  clients.filter(client => {
                    const lastActivity = new Date(client.lastActivity);
                    const today = new Date();
                    const diffTime = Math.abs(today.getTime() - lastActivity.getTime());
                    const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
                    return diffDays <= 7;
                  }).length
                )}
              </div>
              <p className="text-xs text-muted-foreground">
                Active in last 7 days
              </p>
              <div className="mt-2">
                <Progress value={75} className="h-2" />
                <p className="text-xs text-gray-500 mt-1">75% engagement</p>
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Client Analysis Data */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          <Card>
            <CardHeader>
              <CardTitle>Client Status Distribution</CardTitle>
              <CardDescription>
                Overview of client statuses across the platform
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-4">
                {['Active', 'Pending', 'Inactive'].map((status) => {
                  const count = clients.filter(c => c.status === status).length;
                  const percentage = clients.length > 0 ? (count / clients.length) * 100 : 0;
                  
                  return (
                    <div key={status} className="flex items-center justify-between">
                      <div className="flex items-center space-x-3">
                        <Badge variant={getStatusBadgeVariant(status)}>
                          {status}
                        </Badge>
                        <span className="text-sm text-gray-600">{count} clients</span>
                      </div>
                      <div className="flex items-center space-x-2">
                        <div className="w-24 bg-gray-200 rounded-full h-2">
                          <div
                            className="bg-blue-600 h-2 rounded-full"
                            style={{ width: `${percentage}%` }}
                          ></div>
                        </div>
                        <span className="text-sm font-medium w-12">{percentage.toFixed(0)}%</span>
                      </div>
                    </div>
                  );
                })}
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle>Recent Client Activity</CardTitle>
              <CardDescription>
                Latest client interactions and updates
              </CardDescription>
            </CardHeader>
            <CardContent>
              <div className="space-y-3">
                {clients.slice(0, 4).map((client) => (
                  <div key={client.id} className="flex items-center space-x-3 p-2 bg-gray-50 rounded-lg">
                    <div className="w-8 h-8 bg-blue-100 text-blue-800 rounded-full flex items-center justify-center text-sm font-medium">
                      {client.name.charAt(0)}
                    </div>
                    <div className="flex-1">
                      <p className="text-sm font-medium">{client.name}</p>
                      <p className="text-xs text-gray-500">Last active: {formatDate(client.lastActivity)}</p>
                    </div>
                    <Badge variant={getStatusBadgeVariant(client.status)} className="text-xs">
                      {client.status}
                    </Badge>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Scrolling Clients List */}
        <Card>
          <CardHeader>
            <CardTitle>All Clients</CardTitle>
            <CardDescription>
              Complete list of all clients with scrollable interface
            </CardDescription>
          </CardHeader>
          <CardContent>
            {error && (
              <Alert variant="destructive" className="mb-4">
                <AlertDescription>{error}</AlertDescription>
              </Alert>
            )}

            {isLoading ? (
              <div className="space-y-3">
                {[...Array(5)].map((_, i) => (
                  <div key={i} className="flex space-x-4 p-4 border rounded-lg">
                    <Skeleton className="h-12 w-12 rounded-full" />
                    <div className="flex-1 space-y-2">
                      <Skeleton className="h-4 w-[200px]" />
                      <Skeleton className="h-3 w-[150px]" />
                    </div>
                    <Skeleton className="h-6 w-16" />
                  </div>
                ))}
              </div>
            ) : clients.length === 0 ? (
              <div className="text-center py-8">
                <p className="text-gray-500">No clients found</p>
              </div>
            ) : (
              <ScrollArea className="h-96 w-full">
                <div className="space-y-3 pr-4">
                  {clients.map((client) => (
                    <div 
                      key={client.id} 
                      className="flex items-center space-x-4 p-4 border rounded-lg hover:bg-gray-50 hover:shadow-md transition-all cursor-pointer"
                      onClick={() => handleClientClick(client.id)}
                    >
                      <div className="w-12 h-12 bg-gradient-to-br from-blue-400 to-blue-600 text-white rounded-full flex items-center justify-center text-lg font-semibold">
                        {client.name.charAt(0)}
                      </div>
                      <div className="flex-1 min-w-0">
                        <div className="flex items-center space-x-2 mb-1">
                          <h3 className="text-sm font-semibold text-gray-900 truncate">
                            {client.name}
                          </h3>
                          <Badge variant={getStatusBadgeVariant(client.status)} className="text-xs">
                            {client.status}
                          </Badge>
                        </div>
                        <p className="text-sm text-gray-600 truncate">{client.company}</p>
                        <div className="flex items-center space-x-4 mt-1 text-xs text-gray-500">
                          <span>📧 {client.email}</span>
                          <span>📞 {client.phone}</span>
                        </div>
                      </div>
                      <div className="text-right">
                        <p className="text-xs text-gray-500">Created</p>
                        <p className="text-sm font-medium">{formatDate(client.createdDate)}</p>
                        <p className="text-xs text-gray-500 mt-1">Last active</p>
                        <p className="text-sm">{formatDate(client.lastActivity)}</p>
                      </div>
                    </div>
                  ))}
                </div>
              </ScrollArea>
            )}
          </CardContent>
        </Card>

        {/* Integration Status */}
        <Card>
          <CardHeader>
            <CardTitle>Integration Status</CardTitle>
            <CardDescription>
              Current status of external API integrations
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              <div className="flex items-center space-x-3 p-3 bg-green-50 rounded-lg">
                <div className="w-3 h-3 bg-green-500 rounded-full"></div>
                <div>
                  <p className="font-medium text-green-900">Bitrix24</p>
                  <p className="text-sm text-green-700">Connected</p>
                </div>
              </div>
              
              <div className="flex items-center space-x-3 p-3 bg-green-50 rounded-lg">
                <div className="w-3 h-3 bg-green-500 rounded-full"></div>
                <div>
                  <p className="font-medium text-green-900">Amazon S3</p>
                  <p className="text-sm text-green-700">Connected</p>
                </div>
              </div>
              
              <div className="flex items-center space-x-3 p-3 bg-green-50 rounded-lg">
                <div className="w-3 h-3 bg-green-500 rounded-full"></div>
                <div>
                  <p className="font-medium text-green-900">Zoho Books</p>
                  <p className="text-sm text-green-700">Connected</p>
                </div>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>
    </Layout>
  );
}
